@import "../components/variables";
@import "../components/mixin";

body{
  .personal__cabinet{
    padding-top: 25px;
    width: 100%;
    .containerParent();
    .container{
      .containerChild();
      display: grid;
      grid-template-areas: "breadcrumb"
                           "cabinetNav"
                           "order";
      grid-template-columns: 100%;
      .content__cabinet__breadcrumb{
        grid-area: breadcrumb;
        ul {
          display: flex;
          li {
            a {
              font: .875em/1em @sansregular;
              color: #474747;
            }
            span {
              font: .875em/1em @sansregular;
              color: #474747;
            }
          }
        }
      }
      .content__cabinet{
        grid-area: cabinetNav;
        display: grid;
        grid-template-areas: "headline headline"
                             "nav tabs";
        grid-template-columns: 25% 75%;
        h5 {
          grid-area: headline;
          font: 2.25em/2.375em @serifitalic;
          color: @linkColor;
        }
        ul{
          grid-area: nav;
          li{
            width: 250px;
            height: 50px;
            margin: 15px 0;
            text-align: center;
            a{
              height: 50px;
              display: flex;
              justify-content: center;
              align-items: center;
              font: 1.125em/1.5em @sansbold;
              color: #fff;
              text-decoration: none;
              outline: none;
              background-color: @mainColor;
              border-radius: 5px;
            }
          }
        }
        .ui-tabs-active{
          .ui-tabs-anchor{
            color: @linkColor;
            background-color: #f6f6f6;
            border: 1px solid #e6e6e6;
          }
        }
        .profiles,
        .myOrders,
        .featuredProducts{
          grid-area: tabs;
        }
        .profiles{
          width: 670px;
          .custumer__contacts,
          .custumer__email,
          .custumer__password{
            margin: 15px 0;
            width: 100%;
            border: 1px solid #e6e6e6;
            border-radius: 5px;
            h6{
              margin: 30px 30px 40px;
              font: 1.125em/1.25em @sansbold;
              color: @linkColor;
              text-transform: uppercase;
            }
            .contacts{
              display: flex;
              .key{
                p{
                  margin: 0 25px 0 30px;
                  font: 1em/2em @sansbold;
                  color: @linkColor;
                }
              }
              .value{
                p{
                  padding: 0 5px;
                  display: block;
                  font: 1em/2em @sansregular;
                  color: @linkColor;
                }
                form{
                  display: flex;
                  flex-direction: column;
                  position: relative;
                  p{
                    font: 1em/2em @sansbold;
                    color: @linkColor;
                  }
                  input {
                    margin: 3px 0;
                    padding: 0 0 0 10px;
                    width: 250px;
                    height: 20px;
                    font: 1em/1.125em @serifregular;
                    color: #828282;
                    outline: none;
                    border: 2px solid #e6e6e6;
                    border-radius: 5px;
                  }
                  .error{
                    background-color: @errorColor;
                  }
                  .contactsSave{
                    margin: 40px 0 30px -100px;
                  }
                }
              }
            }
            button{
              margin: 40px 0 30px 30px;
              width: 175px;
              height: 50px;
              font: 1em/50px @sansbold;
              color: @mainColor;
              background: #fff;
              border: 1px solid #e6e6e6;
              border-radius: 5px;
              outline: none;
              &:hover{
                cursor: pointer;
                color: #fff;
                background: @mainColor;
                border: 1px solid @mainColor;
                transition: .5s;
              }
            }
          }
          .custumer__contacts{
            .contacts{
              .value{

              }
            }
          }
          .custumer__email,
          .custumer__password{
            .contacts{
              .value{
                p{
                  margin: 0 0 0 30px;
                }
              }
            }
          }
          .custumer__password{
            .contacts{
              .value{
                form{
                  input{
                    margin: 10px 0 10px 50px;
                  }
                }
              }
            }
          }
        }
        .myOrders{
          margin: 15px 0;
          width: 670px;
          border: 1px solid #e6e6e6;
          border-radius: 5px;
          h6{
            margin: 30px 30px 40px;
            font: 1.125em/1.25em @sansbold;
            color: @linkColor;
            text-transform: uppercase;
          }
          ul{
            margin: 0 30px;
            li{
              margin: 0;
              width: 100%;
              display: flex;
              justify-content: space-between;
              p{
                padding: 0 5px;
                font: 1em/2em @sansregular;
                color: @linkColor;
                text-decoration: underline;
                &:hover{
                  cursor: pointer;
                }
              }
              span{
                font: 1em/2em @sansbold;
                color: @linkColor;
              }
            }
          }
        }
        .featuredProducts{
          display: flex;
          flex-wrap: wrap;
          div{
            width: 225px;
            height: 315px;
            margin: 10px;
            position: relative;
            text-align: center;
            border: 1px solid #e6e6e6;
            border-radius: 5px;
            a {
              display: block;
              font: 1em/1.125em @sansbold;
              color: @linkColor;
              text-align: center;
              text-decoration: none;
            }
            img {
              margin-top: 15px;
              width: 175px;
              height: 165px;
            }
            p {
              padding: 10px;
              font: 1em/1.125em @sansregular;
              color: @linkColor;
              text-align: left;
            }
            .price {
              font: 1.25em/1.375em @sansbold;
              span {
                font: .875em/1em @sansregular
              }
            }
            .hover{
              padding: 0;
              margin: 0;
              width: 100%;
              height: 50px;
              display: none;
              flex-direction: row-reverse;
              justify-content: center;
              align-items: flex-end;
              position: absolute;
              left: -1px;
              bottom: -25px;
              z-index: 2;
              background-color: #fff;
              border: 1px solid #e6e6e6;
              border-top: none;
              .addBusket,
              .addFavorite{
                margin: 25px 5px 10px;
                display: block;
                height: 40px;
                position: relative;
                font: 18px/40px @sansregular;
                border: none;
                border-radius: 5px;
                outline: none;
                z-index: 1;
                &:hover{
                  cursor: pointer;
                }
              }
              .addBusket{
                width: 150px;
                color: #fff;
                background-color: @mainColor;
                span{
                  margin: 0 0 0 10px;
                }
              }
              .addFavorite{
                width: 40px;
                color: @linkColor;
                background-color: #fff;
                border: 1px solid @linkColor;
                box-sizing: border-box;
              }
            }
            &:hover {
              cursor: pointer;
              .hover{
                display: flex;
                cursor: default;
              }
            }
          }
        }
      }
      .content__howToMakeOrder{
        grid-area: order;
      }
    }
  }
  .orderInfo{
    width: 845px;
    position: relative;
    text-align: center;
    border-radius: 5px;
    background: #fff;
    .icon-cancel {
      font-size: 1.375em;
      text-align: right;
      color: @mainColor;
    }
    #infoClose {
      width: 21px;
      height: 21px;
      position: absolute;
      top: 6px;
      right: 10px;
      cursor: pointer;
      display: block;
    }
    h5 {
      margin: 0 0 30px;
      padding: 25px 0 0;
      font: 1.125em/1.25em @sansbold;
      color: @mainColor;
    }
    ul{
      margin: 25px 20px;
      padding: 20px;
      border: 2px solid #e6e6e6;
      border-radius: 15px;
      li{
        margin: 25px 0;
        width: 100%;
        div{
          display: flex;
          justify-content: space-between;
          align-items: center;
          .value{
            font: 1em/1.125em @sansregular;
            color: @linkColor;
            text-decoration: none!important;
            &:hover{
              text-decoration: underline!important;
            }
          }
          .quantity{
            width: unset;
            span{
              display: inline-block;
              width: 25px;
              height: 40px;
              font: 1.375em/40px @sansregular;
              text-align: center;
              color: @linkColor;
              background-color: #77c9cc;
              user-select: none;
              &:hover{
                background-color: @mainColor;
                cursor: pointer;
                transition: .5s;
              }
            }
            .minus{
              border-top-left-radius: 5px;
              border-bottom-left-radius: 5px;
            }
            .plus{
              border-top-right-radius: 5px;
              border-bottom-right-radius: 5px;
            }
            input{
              width: 35px;
              height: 34px;
              font-size: 1em;
              font-family: @sansregular;
              text-align: center;
              color: @linkColor;
              border: 2px solid #77c9cc;
              outline: none;
            }
          }
          .price{
            font: 1.5em/1.625em @sansbold;
            color: @linkColor;
            span{
              font: .625em/.75em @sansregular;
            }
          }
          .delete{
            display: inline-block;
            font-size: 1em;
            color: @mainColor;
            &:hover{
              cursor: pointer;
            }
          }
        }
        &:first-child{
          margin: 0 0 25px;
        }
        &:last-child{
          margin: 25px 0 0;
        }
      }
    }
    .total{
      margin: 25px 0 0 25px;
      display: flex;
      h6{
        font: 1.125em/1.25em @sansbold;
        color: @linkColor;
        text-transform: unset;
      }
      p{
        margin-left: 50px;
        font: 1.125em/1.25em @sansbold;
        color: @linkColor;
        span{
          font: .75em/1.125em @sansregular;
        }
      }
    }
    button{
      margin: 30px 0 25px;
      width: 250px;
      height: 40px;
      font: .875em/35px @sansregular;
      color: #fff;
      text-transform: uppercase;
      background-color: @mainColor;
      border: none;
      border-radius: 5px;
      outline: none;
      &:hover{
        cursor: pointer;
      }
    }
  }
}