.containerParent {
  display: grid;
  grid-template-areas: ". container .";
  grid-template-columns: 7% 86% 7%;
}
.containerChild {
  grid-area: container;
}
body .personal__cabinet {
  padding-top: 25px;
  width: 100%;
  display: grid;
  grid-template-areas: ". container .";
  grid-template-columns: 7% 86% 7%;
}
body .personal__cabinet .container {
  grid-area: container;
  display: grid;
  grid-template-areas: "breadcrumb" "cabinetNav" "order";
  grid-template-columns: 100%;
}
body .personal__cabinet .container .content__cabinet__breadcrumb {
  grid-area: breadcrumb;
}
body .personal__cabinet .container .content__cabinet__breadcrumb ul {
  display: flex;
}
body .personal__cabinet .container .content__cabinet__breadcrumb ul li a {
  font: 0.875em/1em "PTSans-Regular";
  color: #474747;
}
body .personal__cabinet .container .content__cabinet__breadcrumb ul li span {
  font: 0.875em/1em "PTSans-Regular";
  color: #474747;
}
body .personal__cabinet .container .content__cabinet {
  grid-area: cabinetNav;
  display: grid;
  grid-template-areas: "headline headline" "nav tabs";
  grid-template-columns: 25% 75%;
}
body .personal__cabinet .container .content__cabinet h5 {
  grid-area: headline;
  font: 2.25em/2.375em "PTSerif-Italic";
  color: #0e3a40;
}
body .personal__cabinet .container .content__cabinet ul {
  grid-area: nav;
}
body .personal__cabinet .container .content__cabinet ul li {
  width: 250px;
  height: 50px;
  margin: 15px 0;
  text-align: center;
}
body .personal__cabinet .container .content__cabinet ul li a {
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  font: 1.125em/1.5em "PTSans-Bold";
  color: #fff;
  text-decoration: none;
  outline: none;
  background-color: #00929e;
  border-radius: 5px;
}
body .personal__cabinet .container .content__cabinet .ui-tabs-active .ui-tabs-anchor {
  color: #0e3a40;
  background-color: #f6f6f6;
  border: 1px solid #e6e6e6;
}
body .personal__cabinet .container .content__cabinet .profiles,
body .personal__cabinet .container .content__cabinet .myOrders,
body .personal__cabinet .container .content__cabinet .featuredProducts {
  grid-area: tabs;
}
body .personal__cabinet .container .content__cabinet .profiles {
  width: 670px;
}
body .personal__cabinet .container .content__cabinet .profiles .custumer__contacts,
body .personal__cabinet .container .content__cabinet .profiles .custumer__email,
body .personal__cabinet .container .content__cabinet .profiles .custumer__password {
  margin: 15px 0;
  width: 100%;
  border: 1px solid #e6e6e6;
  border-radius: 5px;
}
body .personal__cabinet .container .content__cabinet .profiles .custumer__contacts h6,
body .personal__cabinet .container .content__cabinet .profiles .custumer__email h6,
body .personal__cabinet .container .content__cabinet .profiles .custumer__password h6 {
  margin: 30px 30px 40px;
  font: 1.125em/1.25em "PTSans-Bold";
  color: #0e3a40;
  text-transform: uppercase;
}
body .personal__cabinet .container .content__cabinet .profiles .custumer__contacts .contacts,
body .personal__cabinet .container .content__cabinet .profiles .custumer__email .contacts,
body .personal__cabinet .container .content__cabinet .profiles .custumer__password .contacts {
  display: flex;
}
body .personal__cabinet .container .content__cabinet .profiles .custumer__contacts .contacts .key p,
body .personal__cabinet .container .content__cabinet .profiles .custumer__email .contacts .key p,
body .personal__cabinet .container .content__cabinet .profiles .custumer__password .contacts .key p {
  margin: 0 25px 0 30px;
  font: 1em/2em "PTSans-Bold";
  color: #0e3a40;
}
body .personal__cabinet .container .content__cabinet .profiles .custumer__contacts .contacts .value p,
body .personal__cabinet .container .content__cabinet .profiles .custumer__email .contacts .value p,
body .personal__cabinet .container .content__cabinet .profiles .custumer__password .contacts .value p {
  padding: 0 5px;
  display: block;
  font: 1em/2em "PTSans-Regular";
  color: #0e3a40;
}
body .personal__cabinet .container .content__cabinet .profiles .custumer__contacts .contacts .value form,
body .personal__cabinet .container .content__cabinet .profiles .custumer__email .contacts .value form,
body .personal__cabinet .container .content__cabinet .profiles .custumer__password .contacts .value form {
