  position: relative;
}
body .personal__cabinet .container .content__cabinet .profiles .custumer__contacts .contacts .value form p,
body .personal__cabinet .container .content__cabinet .profiles .custumer__email .contacts .value form p,
body .personal__cabinet .container .content__cabinet .profiles .custumer__password .contacts .value form p {
  font: 1em/2em "PTSans-Bold";
  color: #0e3a40;
}
body .personal__cabinet .container .content__cabinet .profiles .custumer__contacts .contacts .value form input,
body .personal__cabinet .container .content__cabinet .profiles .custumer__email .contacts .value form input,
body .personal__cabinet .container .content__cabinet .profiles .custumer__password .contacts .value form input {
  margin: 3px 0;
  padding: 0 0 0 10px;
  width: 250px;
  height: 20px;
  font: 1em/1.125em "PTSerif-Regular";
  color: #828282;
  outline: none;
  border: 2px solid #e6e6e6;
  border-radius: 5px;
}
body .personal__cabinet .container .content__cabinet .profiles .custumer__contacts .contacts .value form .error,
body .personal__cabinet .container .content__cabinet .profiles .custumer__email .contacts .value form .error,
body .personal__cabinet .container .content__cabinet .profiles .custumer__password .contacts .value form .error {
  background-color: #ffd6d6;
}
body .personal__cabinet .container .content__cabinet .profiles .custumer__contacts .contacts .value form .contactsSave,
body .personal__cabinet .container .content__cabinet .profiles .custumer__email .contacts .value form .contactsSave,
body .personal__cabinet .container .content__cabinet .profiles .custumer__password .contacts .value form .contactsSave {
  margin: 40px 0 30px -100px;
}
body .personal__cabinet .container .content__cabinet .profiles .custumer__contacts button,
body .personal__cabinet .container .content__cabinet .profiles .custumer__email button,
body .personal__cabinet .container .content__cabinet .profiles .custumer__password button {
  margin: 40px 0 30px 30px;
  width: 175px;
  height: 50px;
  font: 1em/50px "PTSans-Bold";
  color: #00929e;
  background: #fff;
  border: 1px solid #e6e6e6;
  border-radius: 5px;
  outline: none;
}
body .personal__cabinet .container .content__cabinet .profiles .custumer__contacts button:hover,
body .personal__cabinet .container .content__cabinet .profiles .custumer__email button:hover,
body .personal__cabinet .container .content__cabinet .profiles .custumer__password button:hover {
  cursor: pointer;
  color: #fff;
  background: #00929e;
  border: 1px solid #00929e;
  transition: .5s;
}
body .personal__cabinet .container .content__cabinet .profiles .custumer__email .contacts .value p,
body .personal__cabinet .container .content__cabinet .profiles .custumer__password .contacts .value p {
  margin: 0 0 0 30px;
}
body .personal__cabinet .container .content__cabinet .profiles .custumer__password .contacts .value form input {
  margin: 10px 0 10px 50px;
}
body .personal__cabinet .container .content__cabinet .myOrders {
  margin: 15px 0;
  width: 670px;
  border: 1px solid #e6e6e6;
  border-radius: 5px;
}
body .personal__cabinet .container .content__cabinet .myOrders h6 {
  margin: 30px 30px 40px;
  font: 1.125em/1.25em "PTSans-Bold";
  color: #0e3a40;
  text-transform: uppercase;
}
body .personal__cabinet .container .content__cabinet .myOrders ul {
  margin: 0 30px;
}
body .personal__cabinet .container .content__cabinet .myOrders ul li {
  margin: 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
}
body .personal__cabinet .container .content__cabinet .myOrders ul li p {
  padding: 0 5px;
  font: 1em/2em "PTSans-Regular";
  color: #0e3a40;
  text-decoration: underline;
}
body .personal__cabinet .container .content__cabinet .myOrders ul li p:hover {
  cursor: pointer;
}
body .personal__cabinet .container .content__cabinet .myOrders ul li span {
  font: 1em/2em "PTSans-Bold";
  color: #0e3a40;
}
body .personal__cabinet .container .content__cabinet .featuredProducts {
  display: flex;
  flex-wrap: wrap;
}
body .personal__cabinet .container .content__cabinet .featuredProducts div {
  width: 225px;
  height: 315px;
  margin: 10px;
  position: relative;
  text-align: center;
  border: 1px solid #e6e6e6;
  border-radius: 5px;
}
body .personal__cabinet .container .content__cabinet .featuredProducts div a {
  display: block;
  font: 1em/1.125em "PTSans-Bold";
  color: #0e3a40;
  text-align: center;
  text-decoration: none;
}
body .personal__cabinet .container .content__cabinet .featuredProducts div img {
  margin-top: 15px;
  width: 175px;
  height: 165px;
}
body .personal__cabinet .container .content__cabinet .featuredProducts div p {
  padding: 10px;
  font: 1em/1.125em "PTSans-Regular";
  color: #0e3a40;
  text-align: left;
}
body .personal__cabinet .container .content__cabinet .featuredProducts div .price {
  font: 1.25em/1.375em "PTSans-Bold";
}
body .personal__cabinet .container .content__cabinet .featuredProducts div .price span {
  font: 0.875em/1em "PTSans-Regular";
}
body .personal__cabinet .container .content__cabinet .featuredProducts div .hover {
  padding: 0;
  margin: 0;
  width: 100%;
  height: 50px;
  display: none;
  flex-direction: row-reverse;
  justify-content: center;
  align-items: flex-end;
  position: absolute;
  left: -1px;
  bottom: -25px;
  z-index: 2;
  background-color: #fff;
  border: 1px solid #e6e6e6;
  border-top: none;
}
body .personal__cabinet .container .content__cabinet .featuredProducts div .hover .addBusket,
body .personal__cabinet .container .content__cabinet .featuredProducts div .hover .addFavorite {
  margin: 25px 5px 10px;
  display: block;
  height: 40px;
  position: relative;
  font: 18px/40px "PTSans-Regular";
  border: none;
  border-radius: 5px;
  outline: none;
  z-index: 1;
}
body .personal__cabinet .container .content__cabinet .featuredProducts div .hover .addBusket:hover,
body .personal__cabinet .container .content__cabinet .featuredProducts div .hover .addFavorite:hover {
  cursor: pointer;
}
body .personal__cabinet .container .content__cabinet .featuredProducts div .hover .addBusket {
  width: 150px;
  color: #fff;
  background-color: #00929e;
}
body .personal__cabinet .container .content__cabinet .featuredProducts div .hover .addBusket span {
  margin: 0 0 0 10px;
}
body .personal__cabinet .container .content__cabinet .featuredProducts div .hover .addFavorite {
  width: 40px;
  color: #0e3a40;
  background-color: #fff;
  border: 1px solid #0e3a40;
  box-sizing: border-box;
}
body .personal__cabinet .container .content__cabinet .featuredProducts div:hover {
  cursor: pointer;
}
body .personal__cabinet .container .content__cabinet .featuredProducts div:hover .hover {
  display: flex;
  cursor: default;
}
body .personal__cabinet .container .content__howToMakeOrder {
  grid-area: order;
}
body .orderInfo {
  width: 845px;
  position: relative;
  text-align: center;
  border-radius: 5px;
  background: #fff;
}
body .orderInfo .icon-cancel {
  font-size: 1.375em;
  text-align: right;
  color: #00929e;
}
body .orderInfo #infoClose {
  width: 21px;
  height: 21px;
  position: absolute;
  top: 6px;
  right: 10px;
  cursor: pointer;
  display: block;
}
body .orderInfo h5 {
  margin: 0 0 30px;
  padding: 25px 0 0;
  font: 1.125em/1.25em "PTSans-Bold";
  color: #00929e;
}
body .orderInfo ul {
  margin: 25px 20px;
  padding: 20px;
  border: 2px solid #e6e6e6;
  border-radius: 15px;
}
body .orderInfo ul li {
  margin: 25px 0;
  width: 100%;
}
body .orderInfo ul li div {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
body .orderInfo ul li div .value {
  font: 1em/1.125em "PTSans-Regular";
  color: #0e3a40;
  text-decoration: none!important;
}
body .orderInfo ul li div .value:hover {
  text-decoration: underline!important;
}
body .orderInfo ul li div .quantity {
  width: unset;
}
body .orderInfo ul li div .quantity span {
  display: inline-block;
  width: 25px;
  height: 40px;
  font: 1.375em/40px "PTSans-Regular";
  text-align: center;
  color: #0e3a40;
  background-color: #77c9cc;
  user-select: none;
}
body .orderInfo ul li div .quantity span:hover {
  background-color: #00929e;
  cursor: pointer;
  transition: .5s;
}
body .orderInfo ul li div .quantity .minus {
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
}
body .orderInfo ul li div .quantity .plus {
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
}
body .orderInfo ul li div .quantity input {
  width: 35px;
  height: 34px;
  font-size: 1em;
  font-family: "PTSans-Regular";
  text-align: center;
  color: #0e3a40;
  border: 2px solid #77c9cc;
  outline: none;
}
body .orderInfo ul li div .price {
  font: 1.5em/1.625em "PTSans-Bold";
  color: #0e3a40;
}
body .orderInfo ul li div .price span {
  font: 0.625em/0.75em "PTSans-Regular";
}
body .orderInfo ul li div .delete {
  display: inline-block;
  font-size: 1em;
  color: #00929e;
}
body .orderInfo ul li div .delete:hover {
  cursor: pointer;
}
body .orderInfo ul li:first-child {
  margin: 0 0 25px;
}
body .orderInfo ul li:last-child {
  margin: 25px 0 0;
}
body .orderInfo .total {
  margin: 25px 0 0 25px;
  display: flex;
}
body .orderInfo .total h6 {
  font: 1.125em/1.25em "PTSans-Bold";
  color: #0e3a40;
  text-transform: unset;
}
body .orderInfo .total p {
  margin-left: 50px;
  font: 1.125em/1.25em "PTSans-Bold";
  color: #0e3a40;
}
body .orderInfo .total p span {
  font: 0.75em/1.125em "PTSans-Regular";
}
body .orderInfo button {
  margin: 30px 0 25px;
  width: 250px;
  height: 40px;
  font: 0.875em/35px "PTSans-Regular";
  color: #fff;
  text-transform: uppercase;
  background-color: #00929e;
  border: none;
  border-radius: 5px;
  outline: none;
}
body .orderInfo button:hover {
  cursor: pointer;
}
